<template>
	<div class="header">
		<div class='header-left'>
			<span class="iconfont back-icon">&#xe624;</span>		
		</div>
		<div class='header-input'>
			<span class="iconfont">&#xe632;</span>
			输入城市/景点/游玩主题
		</div>
		<router-link to="/city">
			<div class='header-right'>
				<!-- {{ this.$store.state.city }} 这是共用属性的写法-->
				{{ this.city }}
				<!-- {{ this.doubleCity }} -->
				<span class="iconfont arrow-icon">&#xe64a;</span>
			</div>
		</router-link>
	</div>
</template>

<script>
// 性能优化, vuex提供组件
import { mapState } from 'vuex'
export default {
	name: 'HomeHeader',
	// props: {
	// 	city: String
	// },
	computed: {
		//展开运算符
		...mapState(['city']),
		// ...mapGetters(['doubleCity'])
	}
}
</script>

<style lang="stylus" scoped>
	/*1rem = html font-size=50px*/
	/*@import '../../../assets/styles/varibles.styl'*/
	/*@import '~@/assets/styles/varibles.styl'*/
	/*可以在build.js中找到webpack.base.conf.js文件进行配置简化起别名，然后在命令行重启服务npm run start*/
	@import '~styles/varibles.styl'
	.header
		line-height:$headerHeight
		display:flex
		background:$bgColor
		color:#fff
		.header-left
			flex: 0 0 .64rem
			padding:0 .15rem
			.back-icon
				display:block
				font-size:.4rem
		.header-input
			height:.64rem
			flex:1;
			margin-top:.12rem
			margin-bottom:.12rem
			padding:0 .2rem
			line-height:.64rem
			background:#fff
			border-radius: .1rem
			color:#ccc
		.header-right
			padding: 0 .2rem
			flex: 0 0 1.24rem
			text-align: center
			color:#fff
			.arrow-icon
				font-size:.2rem

</style>


