<template>
	<div class="container" @click="handleGallaryClick">
		<div class="wrapper">
			<swiper :options="swiperOptions">
		    <!-- slides -->
			    <swiper-slide 
			    	v-for="(item, index) of imgs" 
			    	:key="index"
			    >
					<img class="gallary-img" :src="item"/>
				</swiper-slide>
			    
			    <!-- Optional controls slot 插槽-->
			    <div class="swiper-pagination" slot="pagination"></div>
	  		</swiper>
		</div>	
	</div>	
</template>

<script>
export default {
	name: 'CommonGallary',
	props: {
		imgs: {
			type: Array,
			default () {
				return []
			}
		}
	},
	data () {
		return {
			swiperOptions: {
				pagination: '.swiper-pagination',
				paginationType: 'fraction',
				observeParents: true,
				observer: true
			}
		}
	},
	methods: {
		handleGallaryClick () {
			this.$emit('close')
		}
	}
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
	.wrapper >>> .swiper-container
		overflow: inherit
	.container
		column()
		z-index: 99	
		position:fixed
		left:0
		top:0
		right:0
		bottom:0
		background:#000
		.wrapper
			width:100%
			height:0
			padding-bottom:100%
			.gallary-img
				width:100%
			.swiper-pagination
				color:#fff
				bottom:-1rem
</style>


