<template>
	<div class="list" ref="wrapper">
		<!-- ref 可以传递dom -->
		<div>
			<div class="area">
				<div class="title border-topbottom">当前城市</div>
				<div class="button-list">
					<div class="button-wrapper">
						<div class="button">
							<!-- {{ this.$store.state.city }} -->
							{{ this.currentCity }}
						</div>
					</div>
				</div>
			</div>
			<div class="area">
				<div class="title border-topbottom">热门城市</div>
				<div class="button-list">
					<div 
						class="button-wrapper" 
						v-for="item of hot"
						:key="item.id"
						@click="handleCityClick(item.name)"
					>
						<div class="button">{{ item.name }}</div>
					</div>
				</div>
			</div>
			<!-- cities -->
			<div 
				class="area" 
				v-for="(item, key) of cities" 
				:key="key"
				:ref="key"
			>
				<div class="title border-topbottom">{{ key }}</div>
				<ul 
					class="item-list" 
					v-for="innerItem of item" 
					:key="innerItem.id"
					@click="handleCityClick(innerItem.name)"
				>
					<li class="item border-bottom">
						{{ innerItem.name }}
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapState, mapMutations } from 'vuex' 
export default {
	name: 'CityList',
	// 接收数据
	props: {
		hot: Array,
		cities: Object,
		letter: String
	},
	computed: {
		//运用展开运算符 里面可以是数组也可以是对象
		...mapState({
			currentCity: 'city'
		})
	},
	methods: {
		handleCityClick (city) {
			// this.$store.dispatch('changeCity', city)
			// alert()
			// 可以省略autions方法直接调用mutations方法
			// this.$store.commit('changeCity', city)
			// 使用mapMutations组件简化的写法
			this.changeCity(city)
			// 路由跳转到首页
			this.$router.push('./')
		},
		...mapMutations(['changeCity'])
	},
	// 监听器，监听letter 的变化
	watch: {
		letter () {
			// console.log(this.letter)
			// 用babel scroll 提供的方法scrollToElement
			if (this.letter) {
				const element = this.$refs[this.letter][0]
				// console.log(element) 获取到的是数组 不是一个元素
				this.scroll.scrollToElement(element)
			}
		}
	},
	// 当页面挂载完毕后执行的函数
	mounted () {
		this.scroll = new BScroll(this.$refs.wrapper)
	}
}
</script>

<style lang="stylus" scoped>
	/*1rem = html font-size=50px*/
	@import '~styles/varibles.styl'
	@import '~styles/mixins.styl'
	.border-topbottom
		&:before
			border-color:#ccc
		&:after
			border-color:#ccc
	.border-bottom
		&:before
			border-color:#ccc
	.list
		// 解决撑出来的部分
		overflow:hidden
		position:absolute
		top:1.58rem
		left:0
		right:0
		bottom:0
		.title
			line-height:.54rem
			background:#eee
			padding-left:.2rem
			color:#666
			font-size:.26rem
		.button-list
			padding:.1rem .6rem .1rem .1rem
			flex-wrap()
			box-sizing: $boxsizing
			.button-wrapper
				flex:0 0 29.80%
				border:.02rem solid #ccc
				text-align:center
				margin: .1rem
				border-radius:.06rem
				box-sizing:$boxsizing
				.button
					padding: .1rem 0
		.item-list
			.item
				line-height:.76rem
				padding-left:.2rem
				
</style>


