<template>
	<div class="icons">
		<swiper :options="swiperOption">
			<swiper-slide v-for = '(page, index) of pages' :key="index">
				<div class='icon' v-for ="item of page" :key="item.id">
					<div class='icon-img'>
						<img class="icon-img-content" :src="item.imgUrl"/>
					</div>
					<p class="icon-desc">{{ item.desc }}</p>
				</div>
			</swiper-slide>
		</swiper>
	</div>
</template>

<script>
export default {
	name: 'HomeIcons',
	props: {
		list: Array
	},
	data () {
		return {
			swiperOption: {
				autoplay: false
			}
		}
	},
	computed: {
		// 定义一个pages数据
		pages () {
			const pages = []
			this.list.forEach((item, index) => {
				const page = Math.floor(index / 8)
				if(!pages[page]) {
					pages[page] = []
				}
				pages[page].push(item)
			}) 
			return pages
		}
	}
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
	.icons >>> .swiper-slide
		height: 0
		padding-bottom: 50%
		flx()	
		flex-wrap:wrap
		flex-direction:row
	.icons
		margin:.2rem 0
		.icon
			flex: 0 0 25%
			height:0
			padding-bottom: 25%
			overflow:hidden
			flex-center()
			position:relative
			.icon-img
				position:absolute
				top: 0
				left: 0
				right: 0
				bottom: .44rem
				flex-center()
				box-sizing:border-box
				padding:.1rem
				.icon-img-content
					height: 100%
			.icon-desc
				position:absolute
				left:0
				right:0
				bottom:0
				line-height:.44rem
				color:$darkTextColor
				text-align:center
				ellipsis()		
</style>


