<template>
  <div id="app">
    <!-- 显示的是当前路由地址所对应的内容 -->
    <!-- keep-alive 请求一次页面,放到内存中,再次使用直接从内存从获取 
    	使用keep-alive 会多出一个生命周期函数 activated-->
    <keep-alive exclude="Detail">
    	<router-view/>
	</keep-alive>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style></style>



